@charset "utf-8";
.movie {
  position: relative;
  background-color: #f5f5f5;
  padding: 20px;
  box-shadow: 0px 0px 10px #ccc;
  width: 45%;
  float: left;
  margin: 30px 30px 30px 0px;
  display: flex;
}
.movie img {
  position: relative;
  top: -40px;
  max-width: 200px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0px 20px 20px -12px #515151;
  transition: 0.3s;
}
.movie img:hover {
  transform: perspective(2000px) rotateY(30deg);
}
.movie_data {
}
.movie_title {
  font-size: 20px;
  margin-bottom: 5px;
}
.movie_title,
.movie_year {
  font-weight: bold;
}
.movie_year {
  font-size: 14px;
}
.movie_genres {
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}
.movie_genres_text {
  margin-right: 10px;
  font-size: 14px;
}
