@charset "utf-8";
/* 전체서식 */
* {
  padding: 0;
  margin: 0;
  list-style: none;
  color: #333;
  box-sizing: border-box;
}
body {
  font-family: "맑은 고딕", Arial, Helvetica, sans-serif;
  font-size: 12px;
  color: #333;
  height: 100%;
  background-color: gainsboro;
}
/* 네비서식 */
.nav {
  width: 180px;
  position: fixed;
  top: 200px;
  text-align: center;
}
.nav a {
  display: block;
  text-decoration: none;
  color: #333;
  font-size: 16px;
  width: 100%;
  line-height: 50px;
}
.nav a:hover {
  background-color: #333;
  color: #fff;
}
/* 로딩이미지 */
.load_img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
/* 헤더 pc버전 */
@media screen and (min-width: 768px) {
  .header {
    position: fixed;
    left: 0;
    top: 0;
    width: 180px;
    height: 100vh;
  }
  .header h1 {
    text-align: center;
    padding: 15px 0px;
  }
  main {
    position: absolute;
    width: calc(100% - 180px);
    right: 0;
    padding: 30px;
  }
}
/* 헤더 모바일 버전 */
@media screen and (max-width: 767px) {
  .header {
    position: absolute;
    width: 100%;
    top: 0px;
    height: 80px;
  }

  .header h1 {
    line-height: 80px;
    text-align: center;
  }
  main {
    padding-top: 80px;
  }
}
